@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', Arial, Helvetica, sans-serif;
  height: 100vh;
  width: 100vw;
}

#root {
  height: 100%;
  width: 100%;
  background: white;
}

.mainApp {
  max-width: 1200px;
  height: 100%;
  margin: 0 auto;
  background-color: whitesmoke;
}

.header {
  box-sizing: border-box;
  width: 100%;
  min-height: 120px;
  padding: 10px auto 0 auto;
}

.headerColor1 {
  background-color: whitesmoke;
  color: black;
}

.headerColor2 {
  background-color: rgb(189, 14, 14);
  color: rgb(230, 185, 71);
}

.headerColor3 {
  background-color: rgb(223, 219, 219);
}

.headerColor3 h1 {
  background-image: -webkit-gradient( linear, left top, left bottom, from(rgb(219, 48, 48)), color-stop(rgb(219, 48, 48)), color-stop(rgb(56, 219, 50)), 
  color-stop(rgb(21, 62, 197)),color-stop(rgb(247, 217, 51)), to(rgb(247, 217, 51)));
  background-image: linear-gradient( rgb(219, 48, 48), rgb(219, 48, 48), rgb(56, 219, 50), 
  rgb(21, 62, 197),rgb(247, 217, 51), rgb(247, 217, 51));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.headerColor4 {
  background-image: linear-gradient(135deg, rgb(31,191,184), rgb(5,113,108),rgb(25,120,165), rgb(3,17,99));
  color: rgb(77, 229, 240); 
  text-shadow: rgb(65, 59, 59) 3px 3px 3px;
}

.headlineBtn {
  box-sizing: border-box;
  display: flex;
  margin-left: 4%;
  margin-top: 0;
  margin-right: 4%;
  padding: 10px 0;
  justify-content: space-between;
} 

.headline {
  margin: 0;
  padding: 0;
}

.changingAreaApp {
  box-sizing: border-box;
  width: 100%;
  min-height: calc(100% - 120px);
  background-image: url(/portfolio/image/home-office-599475_1920.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-color: rgb(81, 83, 83);
}

.header button {
  width: 50px;
  height: 25px;
  margin-left: 5px;
  margin-top: calc(23px / 2);
  border-style: none;
  border-radius: 5%;
  cursor: pointer;
  display: block;
  

}

.germanFlag {
  background: url(/portfolio/image/german.png);
  background-size: contain;
}

.englishFlag {
  background: url(/portfolio/image/english.png);
  background-size: contain;
}

@media only screen and (min-width: 375px) and (max-width: 599px) {
  .headline {
    font-size: 1.1em;
  }

  .header {
    min-height: 90px;
  }

  .header button {
    margin-top: 0;
  }

  .changingAreaApp {
    min-height: calc(100% - 90px);
  }
}

@media only screen and (max-width: 374px) {
  .headline {
    font-size: 1em;
  }

  .header {
    min-height: 90px;
  }

  .header button {
    margin-top: 0;
  }

  .changingAreaApp {
    min-height: calc(100% - 90px);
  }
}


