    display: flex;
    justify-content: space-evenly;
}
.navBg2 {
    background-color:rgb(230, 185, 72); 
}

.navBgTrans {
    background-color:transparent; 
}

.linkNavbar {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 20%;
    height: 30px;
    text-align: center;
    text-decoration: none;
    padding: 5px;
}

/* .linkNavbar:hover {
    text-decoration: underline;
    font-weight: bolder;
} */

.linkColor1 {
    background-color : whitesmoke;
    color: black;
    border-bottom: 1px solid black;
    -webkit-transition: font-size 1s;
    transition: font-size 1s;
}

.linkColor1:hover {
    /* background-color: rgb(148, 144, 144);  */
    /* color: whitesmoke;  */
    /* box-shadow: black 3px 3px 2px; */
    font-size: 1.5em;
}

.linkColor2 {
    background-color : rgb(230, 185, 71);
    color: black;
}

.linkColor2:hover {
    background-color: rgb(83, 80, 80); 
    color: rgb(230, 185, 71); 
    text-decoration: underline;
    font-weight: bolder;
    /* box-shadow: rgb(230, 185, 71) 3px 3px 2px; */
}

.linkColor3 {
    text-shadow: 2px 2px 2px white;
    border-radius: 10px;
}

.linkColor3:hover {
    text-shadow: 2px 2px 2px gray;
    text-decoration: underline;
    font-weight: bolder;
}

.linkColor3:nth-child(1) {
    background-color : rgb(219, 48, 48);
    color: black;
}

.linkColor3:nth-child(1):hover {
    background-color: whitesmoke; 
    border: 1px solid black;
    color:rgb(219, 48, 48);
    /* box-shadow: rgb(219, 48, 48) 3px 3px 2px; */
}

.linkColor3:nth-child(2) {
    background-color : rgb(56, 219, 50);
    color: black;
}

.linkColor3:nth-child(2):hover {
    background-color: whitesmoke; 
    border: 1px solid black;
    color:rgb(56, 219, 50); 
    /* box-shadow: rgb(56, 219, 50) 3px 3px 2px; */
}

.linkColor3:nth-child(3) {
    background-color : rgb(21, 62, 197);
    color: black;
}

.linkColor3:nth-child(3):hover {
    background-color: white;  
    border: 1px solid black;
    color:rgb(21, 62, 197); 
    /* box-shadow: rgb(21, 62, 197) 3px 3px 2px; */
}
.linkColor3:nth-child(4) {
    background-color : rgb(247, 217, 51);
    color: black;
}

.linkColor3:nth-child(4):hover {
    background-color: rgb(255, 255, 255);
    border: 1px solid black; 
    color:rgb(247, 217, 51); 
    /* box-shadow: rgb(247, 217, 51) 3px 3px 2px; */
}

.linkColor4 {
    text-shadow: none;
    background-color : rgb(77, 229, 240);
    color: black;
    border-radius: 20px 20px 0 0;
    -webkit-transition: border-radius 1s, background-color 1s;
    transition: border-radius 1s, background-color 1s;
}

.linkColor4:hover {
    background-color: whitesmoke; 
    border-radius: 0 0 20px 20px;
    text-decoration: underline; 
    font-weight: bolder;
}

